import React,{Component} from 'react'
import Pedidos from './Pedidos'
const url = 'http://localhost:4000'
export default class Home extends Component{
	constructor(props){
		super(props)
		this.state = {main:<div>
			<input id="user" type="text" /> <br/>
			<input id="senha" type="password"/><br/>
			<input type="button" onClick={this._click.bind(this)} value="entrar"/>
			</div>
		}
	}
	async _verify(){
		var user = localStorage.getIytem("user")
		return user
	}
	async _signin(nome){
		localStorage.setItem("user",nome)
	}
	async _login(user,senha){
		var data = JSON.stringify({user,senha})
		let req = await fetch(url + '/garcon',{
			method:'POST',
			headers:{
				'Content-Type':'application/json',
				'Accept':'aplication/json'
			},
			body:data
		})
		let res = await req.json()
		if(res.user){
			this._signin(user)
			this.setState({main:<Pedidos/>})
		}
	}
	_click(){
		var user = document.querySelector('#user').value
		var senha = document.querySelector('#senha').value
		
		this._login(user,senha)
	}
	render(){
	  return (
	   <div>
			{this.state.main}
		</div>
	  )
}
}
